import React, { useState, useEffect } from 'react';
import { Row, Col, Card, Form, Button, Alert } from 'react-bootstrap';
import { lifeTimelineEvents, introGuide } from '../../data/SelfExplorationData';

interface JournalEntry {
  id: number;
  date: string;
  title: string;
  content: string;
  tags: string[];
}

interface NewEntry {
  title: string;
  content: string;
  tags: string;
}

const ReflectionJournal: React.FC = (): JSX.Element => {
  const [journalEntries, setJournalEntries] = useState<JournalEntry[]>([]);
  const [editingEntry, setEditingEntry] = useState<JournalEntry | null>(null);
  const [newEntry, setNewEntry] = useState<NewEntry>({
    title: '',
    content: '',
    tags: ''
  });

  useEffect(() => {
    const saved = localStorage.getItem('journalEntries');
    if (saved) {
      setJournalEntries(JSON.parse(saved));
    }
  }, []);

  useEffect(() => {
    localStorage.setItem('journalEntries', JSON.stringify(journalEntries));
  }, [journalEntries]);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setNewEntry(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleCancelEdit = () => {
    setEditingEntry(null);
    setNewEntry({ title: '', content: '', tags: '' });
  };

  const handleEditEntry = (entry: JournalEntry) => {
    setEditingEntry(entry);
    setNewEntry({
      title: entry.title,
      content: entry.content,
      tags: entry.tags.join(', ')
    });
  };

  const handleDeleteEntry = (id: number) => {
    setJournalEntries(journalEntries.filter(entry => entry.id !== id));
  };

  const handleAddEntry = (e: React.FormEvent) => {
    e.preventDefault();
    if (!newEntry.title.trim() || !newEntry.content.trim()) return;

    if (editingEntry) {
      setJournalEntries(journalEntries.map(entry => 
        entry.id === editingEntry.id
          ? {
              ...entry,
              title: newEntry.title,
              content: newEntry.content,
              tags: newEntry.tags.split(',').map(tag => tag.trim())
            }
          : entry
      ));
      setEditingEntry(null);
    } else {
      const newEntryWithId: JournalEntry = {
        ...newEntry,
        id: Date.now(),
        date: new Date().toISOString().split('T')[0],
        tags: newEntry.tags.split(',').map(tag => tag.trim())
      };
      setJournalEntries([newEntryWithId, ...journalEntries]);
    }
    setNewEntry({ title: '', content: '', tags: '' });
  };

  const reflectionPrompts = [
    '',
    '',
    '',
    ''
  ];

  return (
    <div className="reflection-journal-section">
      <h2></h2>
      <p className="lead"></p>

      <Card className="mb-4">
        <Card.Body>
          <h4>{introGuide.title}</h4>
          {introGuide.paragraphs.map((p, i) => <p key={i}>{p}</p>)}
          <ul>
            {introGuide.listItems.map((item, i) => <li key={i}>{item}</li>)}
          </ul>
          <Alert variant="info"><strong></strong>{introGuide.tip}</Alert>
        </Card.Body>
      </Card>

      <Card className="mb-4">
        <Card.Body>
          <h4></h4>
          {lifeTimelineEvents.map(event => (
            <div key={event.id} className="mb-3 p-3 border rounded bg-light">
              <h5>{event.age}: {event.title}</h5>
              <p><strong>:</strong> {event.description}</p>
              <p><strong>:</strong> {event.impact}</p>
              <p><strong>:</strong> {event.emotions.join(', ')}</p>
            </div>
          ))}
        </Card.Body>
      </Card>

      <h2></h2>
      <p className="lead"></p>

      <Row>
        <Col md={8}>
          <Card className="mb-4">
            <Card.Body>
              <h4></h4>
              <Form onSubmit={handleAddEntry}>
                <Form.Group className="mb-3">
                  <Form.Label></Form.Label>
                  <Form.Control name="title" value={newEntry.title} onChange={handleInputChange} placeholder="" />
                </Form.Group>
                <Form.Group className="mb-3">
                  <Form.Label></Form.Label>
                  <Form.Control as="textarea" rows={5} name="content" value={newEntry.content} onChange={handleInputChange} placeholder="..." />
                </Form.Group>
                <Form.Group className="mb-3">
                  <Form.Label></Form.Label>
                  <Form.Control name="tags" value={newEntry.tags} onChange={handleInputChange} placeholder="" />
                </Form.Group>
                <Button type="submit" variant="primary">{editingEntry ? '' : ''}</Button>
                {editingEntry && <Button variant="secondary" className="ms-2" onClick={handleCancelEdit}></Button>}
              </Form>
            </Card.Body>
          </Card>

          <div className="journal-entries">
            <h4></h4>
            {journalEntries.length === 0 ? <p className="text-muted"></p> :
              journalEntries.map(entry => (
                <Card key={entry.id} className="mb-3">
                  <Card.Body>
                    <div className="d-flex justify-content-between align-items-start">
                      <div>
                        <h5>{entry.title}</h5>
                        <p className="text-muted mb-2">{entry.date}</p>
                        <p>{entry.content}</p>
                        <div>{entry.tags.map((tag, i) => <span key={i} className="badge bg-light text-dark me-2 mb-2">{tag}</span>)}</div>
                      </div>
                      <div>
                        <Button variant="link" className="p-0 me-2" onClick={() => handleEditEntry(entry)}></Button>
                        <Button variant="link" className="p-0 text-danger" onClick={() => handleDeleteEntry(entry.id)}></Button>
                      </div>
                    </div>
                  </Card.Body>
                </Card>
              ))}
          </div>
        </Col>

        <Col md={4}>
          <Card className="mb-4">
            <Card.Body>
              <h4></h4>
              <p></p>
              {reflectionPrompts.map((prompt, i) => (
                <div key={i} className="prompt-item p-3 bg-light rounded mb-2 d-flex justify-content-between align-items-start">
                  <p className="mb-0">{prompt}</p>
                  <Button size="sm" onClick={() => setNewEntry(prev => ({ ...prev, content: prev.content + '\n' + prompt }))}></Button>
                </div>
              ))}
            </Card.Body>
          </Card>

          <Card>
            <Card.Body>
              <h4></h4>
              <div className="tag-cloud p-3">
                {["","","","","","","",""].map((tag, i) => (
                  <span key={i} className="badge bg-secondary me-2 mb-2 p-2" role="button" onClick={() => setNewEntry(prev => ({ ...prev, tags: prev.tags ? prev.tags + ', ' + tag : tag }))}>{tag}</span>
                ))}
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default ReflectionJournal;
